<template>
   
    <div id="show-blogs">
 
        <el-row>
        <el-col :span="24" :offset="1" v-for="blog in blogs" :key="blog.id" class="single-blog">
            <router-link v-bind:to="'/blog/' + blog.id">
            <el-card class="card" :body-style="{ padding: '0px' }" shadow="always">
                
               <div><p class="blogTitle">{{blog.title}}</p></div>
               <div class="img"><img v-bind:src=blog.imageUrlBlog height="100px" class="image"/></div>
               <div class="bottom clearfix">
                <time id="showtime">{{ blog.currentDataTime }}</time>
               </div>
            </el-card>

        </router-link>
        </el-col>
        </el-row>

        </div>

   


</template>

<script>


export default {
  components:{

  },
  data(){
    return{
        blogs:[]
    }
},
methods: {
    
},
  created(){
      this.$http.get('https://alanwebsite-484a3.firebaseio.com/posts.json').then(function(data){
         return data.json();
      }).then(function(data){
          var blogsArray = [];
          for(let key in data){
              data[key].id = key
              blogsArray.push(data[key])
          }
          this.blogs = blogsArray;
      });
  }

}
</script>

